import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { addTask } from '../actions'

class NewTask extends React.Component {

	constructor(props) {
		super(props)

		this.state = {
			tags:[],
		}

		this.handleChange = this.handleChange.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this)


	}

	handleSubmit(e) {
		e.preventDefault()

		//Dispatch action!!!!

		console.log(this)

		this.props.addTask({
			title: this.state.title,
			goal : this.state.goal,
			tags : this.state.tags,
		})

		this.setState({
			title:'',
			goal:'',
			tags:[],
		})
	}

	isOn(e){
		e.preventDefault()
		e.isTagOn = 'tagIsOn'
	}

	handleChange(event) {
		this.setState({
			[event.target.name]: event.target.value
		})
	}




	render() {
		// let isTagOn = ''

		return (
			<form onSubmit={this.handleSubmit}>
				<label>
					Task:
					<input placeholder="Task Title" autoComplete="off" name="title" type="text" onChange={this.handleChange} value={this.state.title}  />
				</label>
				{
				// 	<label>
				// 	Task Objective:
				// 	<input placeholder = "Task objective" autoComplete="off" type="text" name = "goal" onChange={this.handleChange} value={this.state.goal} />
				// </label>
			}
				<button id="submitBtn" type="submit" value="Submit" onChange={this.handleSubmit}>submit</button>
			</form>
		)
	}
}

NewTask.propTypes = {
	addTask: PropTypes.func.isRequired,
}

export default connect(
	state => ({
		tasks: state.tasks,
		tags: state.tags
	}), {
		addTask,
	}
)(NewTask)



